import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import * as moment from 'moment';
import { AddContacformComponent } from '../add-contacform/add-contacform.component';
import { ContactUsServiceService } from 'src/app/core/services/contact-us-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';
import { AddTicketComponent } from '../add-ticket/add-ticket.component';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { CommonService } from 'src/app/core/services/common.service';

interface IRange {
  value: Date[];
  label: string;

}
@Component({
  selector: 'app-newticketpage',
  templateUrl: './newticketpage.component.html',
  styleUrls: ['./newticketpage.component.scss']
})
export class NewticketpageComponent implements OnInit {
  public isChecked: boolean = false;
  public archiveIconfalse: boolean = false;
  // public tickets = [];

  public upiCheckedId = [];
  public checkSelectsingle: boolean = false;
  public checkSingleArray: any = [];
  public form: FormGroup;
  public allSelect: boolean = false;
  public checkSelect: boolean = false;
  public checkSelectsinglefalse: boolean = false;
  public contactListResponse: any = [];
  public search: any;
  public searchNot: boolean = false;
  public searchNot1: boolean = true;
  public quarterStartDate: any;
  public quarterEndDate: any;
  public today: Date;
  public quarter: any;
  public maxDate: Date;
  public start = moment().subtract(29, 'days');
  public end = moment();
  public ranges: IRange[];
  public dateRange: Date[];
  public currentpage: any;
  public totalrow: any;
  public toatlPages: any;
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;
  public pagesize: number = 1;
  public p = 1;
  public ticketId: any;
  public tikId: any;
  public userId: any;
  statusFlag:boolean=false;
  public searchKey: any;
  tickets: any[] = [];
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))

  constructor(private dialog: MatDialog,
    public contactService: ContactUsServiceService,
    private titleCasePipe: TitleCasePipe,
    private formBuilder: FormBuilder,
    private commonService: CommonService,
    private toastService: ToastNotificationService) {
    this.today = new Date();
    this.maxDate = new Date(this.today.getFullYear(), this.today.getMonth(), 25);
    let quarter = moment(this.today).utc().quarter() //outputs 4
  }

  ngOnInit(): void {
    this.dateRange = [new Date(new Date().setDate(new Date().getDate() - 30)), new Date(new Date().setDate(new Date().getDate()))]

    this.ranges = [

      {
        value: [new Date(new Date().setDate(new Date().getDate() - 7)), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'Last 7 Days'
      },
      {
        value: [new Date(new Date().setDate(new Date().getDate() - 30)), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'Last 30 Days'
      },
      {
        value: [new Date(new Date(new Date(new Date().setMonth(new Date().getMonth() - 1)).setDate(1)).toDateString()), new Date(new Date(new Date().setDate(0)).toDateString())],
        label: 'Last Month'

      },
      // {
      //   value: [this.quarterStartDate._d, this.quarterEndDate._d],
      //   label: ' Last Quarter'
      // },
      {
        value: [new Date(new Date(new Date().getFullYear() - 1, 3, 1).toDateString()), new Date(new Date(new Date().getFullYear(), 2, 31).toDateString())],
        label: 'Last Financial Year'

      },
      {
        value: [new Date(new Date(new Date().getFullYear(), 3, 1).toDateString()), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'This Financial Year'

      },
      {
        value: [],
        label: 'Custom Range'

      }];
    this.contactService.partyAdded$.subscribe(() => {
      this.getAllCotactEn();
    });
    this.commonService.rowAdded$.subscribe(() => {
      this.getAllCotactEn();
     });
    this.getAllCotactEn();
    this.form = this.formBuilder.group({
      checkArray: this.formBuilder.array([]),
    });

  }

  getStatus(id,status){
    this.ticketId =id;
    if(status== "SOLVED")  {
      this.statusFlag= true;
    }
    else{
      this.statusFlag=false
    }


  }

  getAllCotactEn() {
    this.contactService.getAllContactEnDetails({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo + 1),moment(this.dateRange[0], 'DD-MMM-YYYY').format('YYYY-MM-DD') ,moment(this.dateRange[1], 'DD-MMM-YYYY').format('YYYY-MM-DD')).then((res:any) => {
      this.p = res.data.currentpage;
      this.totalrow = res.data.totalrows
      this.contactListResponse = res.data.pageData
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

  getEnquirysearch(search) {
    this.search = search;
    this.searchNot = true;
    this.searchNot1 = false;
    if (this.searchKey == "") {
    this.contactService.getEnquiriesList({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo + 1), this.search).then((res) => {
      this.contactListResponse = res.data.pageData
      this.totalrow = res.data.totalrows
    })
  }
  else {
    this.contactService.getEnquiriesList({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo + 1), this.search).then((res) => {
      this.contactListResponse = res.data.pageData
      this.totalrow = res.data.totalrows
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
      }

    }
    );

  }
}

  getcontact(ticketId: any) {
  }

  removeTicket() {
    this.contactService.deleteticketById({}, this.ticketId).then((res) => {
      console.log(res.ticketId);
      this.toastService.toastMsg({
        title: "Success",
        content: "Ticket Deleted Successfully!!!"
      });
      // this.toastService.openSnackBar("Ticket Deleted Successfully!!!");
      this.getAllCotactEn();
      this.checkSelectsingle = false;
      this.upiCheckedId = [];
      this.checkSelect = false;
      this.allSelect = false;
    } ,
    (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
      }
    }
    )
  }

  onChekedsingleCheck(e) {
    const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    if (e.target.checked) {
      this.checkSingleArray.push(e.target.value);
    }
    else {
      this.checkSingleArray = [];
    }
  }

  getUPIcheckId(e,ticketId) {
    this.ticketId = ticketId;

  }
  getSingleCheckedId(e,ticketId) {
    this.ticketId = ticketId;

  }
  
  onChekedsingleCheckfalse(e, id) {
    console.log("checked", e.target.checked)

    if (e.target.checked == true) {
      this.archiveIconfalse = true;
      this.checkSingleArray.push(e.target.value);
      console.log("checkSingleArray",this.checkSingleArray)
      this.upiCheckedId = this.checkSingleArray;
    }
    else {
      if (this.upiCheckedId.length > this.checkSingleArray.length) {

        var index1 = this.upiCheckedId.indexOf(id);
        console.log(index1)
        this.upiCheckedId.splice(index1, 1);
        console.log("after single click upiCheckedId", this.upiCheckedId)

        if (this.upiCheckedId.length > 0) {
          this.archiveIconfalse = true;
          this.checkSelect = false;
          this.isChecked=false;
        }
        else {
          this.archiveIconfalse = false;
          this.upiCheckedId = [];
        }

      }
      else {
        var index = this.checkSingleArray.indexOf(id);
        console.log(index)
        this.checkSingleArray.splice(index, 1);
        console.log("this.checkSingleArray", this.checkSingleArray)

        if (this.checkSingleArray.length > 0) {
          this.archiveIconfalse = true;
        }
        else {
          this.archiveIconfalse = false;
          this.checkSingleArray = [];
        }
      }
    }
  }

  onChekMultiple($event) {
    let id = $event.target.value;
    this.isChecked = $event.target.checked;
    console.log("this.isChecked", this.isChecked)
    this.tickets = this.contactListResponse.map((item) => {
      if (id == -1) {
        if (this.isChecked == true) {
          this.archiveIconfalse = true;
          this.checkSelectsinglefalse = true;
          return item;
        }
        else {
          this.archiveIconfalse = false;
          this.checkSelectsinglefalse = false;
          this.upiCheckedId = [];
          this.tickets=[];
        }
      }
    })
    console.log("multiple click", this.tickets)
    for (var i = 0; i < this.tickets.length; i++) {
        this.upiCheckedId.push(this.tickets[i].ticketId);
    }
    console.log("multiple click", this.upiCheckedId)
  }

  closeStatus(){
    let body={
      "ticketId": this.ticketId
    }
    this.contactService.closeticket(body, this.currentUser.id).then((res) => {
      this.toastService.toastMsg({
        title: "Success",
        content: "Ticket Status Solved Successfully!!!"
      });
      this.removeTicket();
  }
  ,
      (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
      })
}
multipleTicketDel(){
  this.contactService.deleteMultipeticket({"ticketsId": this.upiCheckedId}, this.currentUser.id).then((res:any)=>{
    this.getAllCotactEn(); 
    console.log("res")
    this.toastService.toastMsg({
      title: "Success",
      content: "Ticket Deleted Successfully!!!"
    });
    this.checkSelect=false;
    this.archiveIconfalse = false;
    this.allSelect = false;
    this.checkSelectsinglefalse = false;
    this.isChecked=false;
    this.upiCheckedId = [];
    this.checkSingleArray = [];
    this.checkSelectsingle = false;
    this.upiCheckedId = [];
    this.checkSelect = false;
    this.allSelect = false;
  }, (err) => {
    if (err.error.expose) {
      this.toastService.toastMsg({
        title: "Error",
        content: this.titleCasePipe.transform(err.error.error_message),
      });
    }
    else {
      this.toastService.toastMsg({
        title: "Error",
        content: "Something Went Wrong.",
      });
    }
  })
}


closeTicketStatus(){
  let body={
    "ticketId": this.ticketId
  }
  this.contactService.closeMulTicket(body, this.currentUser.id).then((res) => {
    this.toastService.toastMsg({
      title: "Success",
      content: "Ticket Status Solved Successfully!!!"
    });
    this.multipleTicketDel();
}
,
    (err) => {
      console.log("API Error:", err);
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    })
 }
  changeDate(_e: any){
  this.getAllCotactEn();
  }

  openAddTicketForm() {
    const dialogRef: MatDialogRef<AddTicketComponent> = this.dialog.open(AddTicketComponent, {
      width: '',
      height: '',
      data: {},
      panelClass: 'custom-dialog-AddQuery-class',
      disableClose: true,
     
    });
    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });


  }
  public handlePage(e: any) {
    console.log(e);
    // this.tableData = [];
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.contactListResponse.length > 0) {
      this.partyPageSize = e.pageSize;
      const searchInput = document.getElementById('searchInput') as HTMLInputElement;
      if (searchInput.value !== '') {
        this.getEnquirysearch(searchInput.value);
      } else {
        this.getAllCotactEn();
      }
    }
  }
}
