import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ContactUsServiceService } from 'src/app/core/services/contact-us-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-add-ticket',
  templateUrl: './add-ticket.component.html',
  styleUrls: ['./add-ticket.component.scss']
})
export class AddTicketComponent implements OnInit {
  public conactForm: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
	public submitted: boolean = false;

  constructor(private toastService:ToastNotificationService,
    private ContactUsServiceService:ContactUsServiceService, private titleCasePipe: TitleCasePipe, 
    private router: Router,private formBuilder: FormBuilder,public dialogRef: MatDialogRef<AddTicketComponent>) { }

  ngOnInit(): void {
    this.createQuery()
  }
  createQuery(){
    this.conactForm = this.formBuilder.group({
      // ticketType:[],
      ticketType: ['', Validators.required],

      Message:[""],
    });
  }
  get f() {
		return this.conactForm.controls;
	}
  cancel(){
    this.dialogRef.close();
  }
  postQuery(){
    this.submitted = true;
		if (this.conactForm.invalid) {
			console.log(this.conactForm);
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
			// this.toastService.openErrorSnackBar("Please fill mandatory fields!!!");
			return false;
		}
		else {
    let body=
{
  
  "enquiry":{
    "ticketType":this.conactForm.controls.ticketType.value,
    "description":this.conactForm.controls.Message.value,
    "image":""
  },

  "enquiryImages":[],
}
    if (this.conactForm.valid) {
      this.ContactUsServiceService.contactUspost(body,this.currentUser.id).then((res)=>{
        if (res) {
          this.ContactUsServiceService.notifyPartyAdded();
          this.toastService.toastMsg({
            title: "Success",
            content: "Ticket Raised Successfully!!!"
          });
          // this.toastService.openSnackBar("Ticket Raised Successfully !!!");
          this.router.navigate(["/pages/contactlist-page"]);
          this.dialogRef.close();
        }
       
      },(err) => {
        if(err.error.expose){
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else{
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }      
      })  
    }
  }
  }
}
